#page {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  width: 100%;
  color: #fff;
  font-size: 20px;
}
#page .page-title {
    margin: 0 auto 30px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 1000px;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
#page .page-title img {
      margin-right: 30px;
      width: 184px;
      height: 50px;
}
#page main {
    width: 1000px;
    margin: 0 auto;
}
#page main ul {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
}
#page .page-select-li {
    width: 280px;
    height: 200px;
    padding-top: 40px;
    text-align: center;
    box-sizing: border-box;
}
#page .page-select-li a {
      color: inherit;
      font-size: inherit;
}
#page .page-select-li.predict {
      background-color: #d9ac69;
}
#page .page-select-li.re-schedule {
      background-color: #a7c8b9;
}
#page .page-select-li.add {
      padding-top: 0;
      background-color: #ccc;
}
#page .page-select-li.add a {
        font-size: 200px;
        line-height: 150px;
}
#page .page-select-li img {
      display: block;
      margin: 0 auto 30px;
}



