.aps-table {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.aps-table .aps-table-function {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 20px;
            flex: 0 0 20px;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: reverse;
        -ms-flex-direction: row-reverse;
            flex-direction: row-reverse;
    margin-bottom: 10px;
    display: none;
}
.aps-table .aps-table-function i {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 20px;
              flex: 0 0 20px;
      margin: 0 5px;
      height: 20px;
      background: url(../../../assets/table-function.png) #fff left top no-repeat;
      cursor: pointer;
}
.aps-table .aps-table-function .aps-table-order {
      background-position-x: 0;
}
.aps-table .aps-table-function .aps-table-excel {
      background-position-x: -20px;
}
.aps-table .aps-table-function .aps-table-print {
      background-position-x: -40px;
}
.aps-table .aps-table-main {
    border: 1px solid #ccc;
    overflow: auto;
}
.aps-table .aps-table-main table {
      width: 100%;
      text-align: center;
      border-collapse: inherit;
}
.aps-table .aps-table-main table tr th {
        padding: 0 4px;
        height: 36px;
        border: 1px solid #ccc;
        background-color: #1e7cd9;
        color: #fff;
        font-size: 14px;
        font-weight: normal;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
}
.aps-table .aps-table-main table tr td {
        padding: 0 4px;
        background-color: #fff;
        border: 1px solid #ccc;
        border-left: 0;
        border-top: 0;
}
.aps-table .aps-table-main table tr td .aps-table-details {
          width: 12px;
          height: 12px;
          background: url(../../../assets/details.png) #fff left top no-repeat;
          cursor: pointer;
}
.aps-table .aps-table-main table tr:nth-child(even) td {
        background-color: #f0f0f0;
}
.aps-table .aps-table-main table .aps-table-selection label {
        display: inline-block;
        width: 12px;
        height: 12px;
        background: url(../../../assets/cascader-state.png) #fff left center no-repeat;
}
.aps-table .aps-table-main table .aps-table-selection label input {
          display: none;
}
.aps-table .aps-table-page {
    margin-top: 10px;
    text-align: center;
}
.aps-table .aps-table-page .el-input__inner {
      width: 110px;
}



