
.reschedule-dialog-main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: calc(100vh - 300px);
  background-color: #fff;
}
.reschedule-dialog-main .reschedule-dialog-header {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 40px;
            flex: 0 0 40px;
    background-color: #ccc;
}
.reschedule-dialog-main .reschedule-dialog-search {
    border-bottom: 1px solid #ccc;
}
.reschedule-dialog-main .reschedule-dialog-search .search-box {
      margin: 0 10px 10px 0;
}
.reschedule-dialog-main .reschedule-dialog-search .search-box .el-date-editor,
      .reschedule-dialog-main .reschedule-dialog-search .search-box .el-input {
        width: 140px;
        height: 30px;
}
.reschedule-dialog-main .reschedule-dialog-search .search-box .el-date-editor input,
        .reschedule-dialog-main .reschedule-dialog-search .search-box .el-input input {
          width: 140px;
          height: 30px;
}
.reschedule-dialog-main .reschedule-dialog-search .search-button-box {
      min-width: 218px;
}
.reschedule-dialog-main .reschedule-dialog-search .default-btn {
      margin-bottom: 10px;
}
.reschedule-dialog-main .reschedule-dialog-show {
    -webkit-box-flex: 1;
        -ms-flex: 1 1;
            flex: 1 1;
    margin: 10px 0;
    overflow: hidden;
}
.reschedule-dialog-main .aps-dropdown .aps-dropdown-main .aps-dropdown-input {
    width: 140px;
}
.reschedule-dialog-main .look-detail {
    position: absolute;
    max-height: 360px;
    overflow-y: auto;
    border: 1px solid #ccc;
    box-shadow: 0 0 20px #ccc;
    background-color: #fff;
    text-align: center;
}
.reschedule-dialog-main .look-detail table tr {
      border: 1px solid #ccc;
}
.reschedule-dialog-main .look-detail table tr td {
        max-width: 100px;
        padding: 0 4px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
}



